@import url(https://fonts.googleapis.com/css?family=Roboto:500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  padding: 2% 5%;
}

.App * {
  box-sizing: border-box;
}

.title-logo{
  width: 100%;
  max-width: 250px;
  padding-top: 25px;
}

ul {
  padding: 0 !important;
  list-style: none;
}

p {
  margin: 2px 0 !important;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
  width: 250px;
}

.city {
  width: 250px;
}

.CitySearch, .NumberOfEvents {
  max-width: 500px;
  margin: 0 auto;
  padding-top: 20px;
  align-items: center;
  font-size: 12pt;
}

.suggestions {
  width: 250px;
  align-self: center;
  margin: 0 auto;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}

.card {
  padding: 10px;
  margin-bottom: 10px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 4px;
  max-width: 600px;
  margin: 10px auto;
  justify-self: stretch;
}

.card-header {
  font-weight: bold;
  width: 100%;
  padding: 10px !important;
}

.card-text {
  padding: 5px;
  margin: 5px;
}

.event-details {
  padding-top: 20px !important;
}

.event-button {
  width: 200px;
  height: 40px;
  margin: 15px auto 5px;
  border-radius: 4px;
  border: none !important;
  background-image: linear-gradient(to right, #4e6885, #43668b, #4a81a5, #675b96);
  box-shadow: 0 4px 15px 0 rgba(45, 54, 65, 0.75);
  background-size: 300% 100%;
  color: #fff;
  cursor: pointer;
  transition: all .4s ease-in-out;
}

.event-button:hover {
  background-position: 100% 0;
  transition: all .4s ease-in-out;
}

.event-button:focus, .event-button:active {
  outline: none !important;
}

.event-card{
  justify-self: stretch;
  margin: 10px;
}

.EventList {
  justify-items: stretch;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr;
}

.data-vis-wrapper {
  justify-items: stretch;
  align-items: center;
  display: grid;
  grid-template-columns: 1fr;
  font-size: 14tpt;
}

.recharts-responsive-container {
  width: 100%;
  padding-bottom: 100px;
  margin: 20px 0px;
}

.App-Data_Visualization {
  padding: 20px 0px;
}

.scatter-chart {
  margin: 0 auto;
  padding-right: 20px;
}

.pie-chart {
  margin: 0 auto;
  padding-left: 50px;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.Alert {
  top: 20px;
}

.recharts-surface {
  overflow: visible;
}

.collapse {
  visibility: hidden;
}
.collapse.show{
  visibility: visible;
}

@media only screen and (min-width: 992px) {
  .EventList {
    grid-template-columns: 1fr 1fr;
  }

  .data-vis-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  right: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
          animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


.WelcomeScreen {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: white;
  text-align: center;
  padding: 3% 5%;
  box-sizing: border-box;
}
.login-text {
  padding: 50px 20px 15px 20px;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}
.google-btn {
  width: 200px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
  background: #1669f2;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px !important;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.btn-text {
  float: right;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
  background-color: #4285f4;
  width: 80%;
  height: 100%;
  text-align: center;
}
